import React from 'react';
import { assets } from '@/assets/assets';
import Image from 'next/image';

const Message = ({ role, content }) => {
  return (
    <div className="flex flex-col items-center w-full max-w-3xl text-sm">
      <div className={`flex flex-col w-full mb-8 ${role === 'user' && 'items-end'}`}>
        <div
          className={`group relative flex max-w-2xl py-3 rounded-xl ${
            role === 'user'
              ? 'bg-[#414158] px-5 text-white'
              : 'bg-[#e5e5ea] text-black gap-3 px-5'
          }`}
        >
          <div
            className={`opacity-0 group-hover:opacity-100 absolute ${
              role === 'user' ? '-left-16 top-2.5' : 'left-9 -bottom-6'
            } transition-all`}
          >
            <div className="flex items-center gap-2 opacity-70">
              {role === 'user' ? (
                <>
                  <Image src={assets.copy_icon} alt="copy" className="w-4 cursor-pointer" />
                  <Image src={assets.pencil_icon} alt="edit" className="w-4.5 cursor-pointer" />
                </>
              ) : (
                <>
                  <Image src={assets.copy_icon} alt="copy" className="w-4.5 cursor-pointer" />
                  <Image src={assets.regenerate_icon} alt="regen" className="w-4 cursor-pointer" />
                  <Image src={assets.like_icon} alt="like" className="w-4.5 cursor-pointer" />
                  <Image src={assets.dislike_icon} alt="dislike" className="w-4 cursor-pointer" />
                </>
              )}
            </div>
          </div>

          {role === 'user' ? (
            <span>{content}</span>
          ) : (
            <>
              <Image
                src={assets.logo_icon}
                alt="bot"
                className="h-9 w-9 p-1 border border-white/15 rounded-full"
              />
              <div className="space-y-4 w-full">{content}</div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default Message;
